<template>
  <div class="box">
    <div class="cart-item__details">
      <p class="is-inline">{{ cartItem.product.title }}</p>
      <div>
        <span class="cart-item--price has-text-info has-text-weight-bold">
          ${{ cartItem.product.price / 100 }} X {{ cartItem.quantity }}
        </span>
        <button class="button is-danger is-outlined" @click="incrementQuantity">
          +
        </button>
        <span>
          <i
            class="fa fa-arrow-circle-up cart-item__modify"
            @click="addCartItem(cartItem)"
          ></i>
          <i
            class="fa fa-arrow-circle-down cart-item__modify"
            @click="removeCartItem(cartItem)"
          ></i>
        </span>
      </div>
    </div>
  </div>
</template>
<script>
import { mapActions } from "vuex";
export default {
  name: "CartListItem",
  props: ["cartItem"],
  methods: {
    ...mapActions(["addCartItem", "removeCartItem"]),
    incrementQuantity() {
      this.$store.dispatch("updateCartItemQuantity", {
        productId: this.cartItem.productId,
        quantity: this.cartItem.quantity + 1,
      });
    },
    decrementQuantity() {
      this.$store.dispatch("updateCartItemQuantity", {
        productId: this.cartItem.productId,
        quantity: this.cartItem.quantity - 1,
      });
    },
  },
};
</script>
